@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

@import './header';
@import './footer';

.section {
  padding: 40px 0;

  @include mixin.small-tablet {
    padding: 24px 0;
  }

  @include mixin.phone {
    padding: 16px 0;
  }

  &--big {
    padding: 56px 0;

    @include mixin.small-tablet {
      padding: 32px 0;
    }
  }

  &--small {
    padding: 16px 0;
  }

  &__head {
    text-align: center;

    h2 {
      margin-bottom: 12px;
    }

    p {
      margin: 0;
    }
  }
}