@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

.footer {
  &__middle {
    display: flex;
    flex-wrap: wrap;
    gap: 0 20px;

    @include mixin.small-tablet {
      flex-direction: column;
    }
  }

  &__middle-list {
    flex-grow: 1;
  }

  &__middle-list-head {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 600;
    line-height: 1.2;
    padding: 10px 0;
    pointer-events: none;
    transition: 0.3s linear;

    @include mixin.small-tablet {
      pointer-events: unset;
      padding: 8px 0;
      cursor: pointer;
    }

    svg {
      display: none;
      transform-origin: center;
      transition: 0.3s linear;

      @include mixin.small-tablet {
        display: block;
      }

      path {
        transition: 0.3s linear;
      }
    }

    &:not(.collapsed) {
      @include mixin.small-tablet {
        color: var(--primary-darker, #1874c1);

        svg {
          transform: rotateZ(-180deg);

          path {
            fill: var(--primary-darker, #1874c1);
          }
        }
      }
    }
  }

  &__middle-list-body {
    list-style: none;
    margin: 0;
    padding: 0;

    &.collapse {
      display: block;

      @include mixin.small-tablet {
        &:not(.show) {
          display: none;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap-reverse;
    gap: 0 25px;

    a {
      font-size: 14px;
      font-weight: 400;
      line-height: 1.5;
      color: var(--secondary-darker, #6a6a6a);

      &:hover,
      &:active {
        color: var(--secondary-darker, #6a6a6a);
      }
    }

    .product-hunt {
      @include mixin.wide-tablet {
        order: 1;
      }
    }

    .policy {
      gap: 32px;

      @include mixin.wide-tablet {
        gap: 24px;
      }

      @include mixin.small-tablet {
        font-size: 12px;
        gap: 10px;
      }

      a {
        @include mixin.small-tablet {
          font-size: 12px;
        }
      }
    }

    a.copyright {
      @include mixin.small-tablet {
        font-size: 12px;
      }
    }
  }

  &__middle-item {
    display: block;
  }
}