@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

.mb-5 {
  @include mixin.phone {
    margin-bottom: 32px !important;
  }
}

.mb-4 {
  margin-bottom: 32px !important;

  @include mixin.phone {
    margin-bottom: 24px !important;
  }
}

.form {
  width: 100%;
  max-width: 296px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-end;

  &__group {
    align-self: stretch;
  }

  &__btn {
    margin-top: 14px;
    font-size: 14px;
  }

  &__hr {
    margin: 24px 0;
    width: 100%;
    height: 1px;
    background: var(--secondary-lighter, #f5f5f5);
  }
}

.custom-input {
  position: relative;
  display: block;
  position: relative;
  padding: 26px 16px 8px 16px;
  background: var(--secondary-lighter, #f5f5f5);
  border-radius: 16px;
  border: none;
  outline: none;
  margin-bottom: 0;
  height: fit-content;

  &--no-label {
    padding: 17px 16px;
  }

  &__value,
  &__placeholder {
    padding: 0;
    outline: none;
    background: transparent;
    font-size: 14px;
    font-weight: 500;
    border: none;
    height: 21px;
    display: block;
    color: var(--secondary-darkest, #000) !important;

    &::placeholder {
      font-size: 14px;
      font-weight: 500;
      color: var(--secondary-darkest, #000) !important;
    }
  }

  &--no-label &__placeholder {
    display: none;
  }

  &--disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  &__value {
    width: 100%;
    pointer-events: none;
  }

  &__placeholder {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  &--active &__placeholder,
  &--valued &__placeholder,
  &__value:focus+&__placeholder,
  &__value:not(:placeholder-shown)~&__placeholder {
    font-size: 12px;
    color: var(--secondary-darker, #6a6a6a);
    transform: translateY(-110%);
  }
}

.selector {
  position: relative;
  display: block;

  &__label {
    position: relative;
    padding: 26px 46px 8px 16px;
    background: var(--secondary-lighter, #f5f5f5);
    border-radius: 16px;
    border: none;
    outline: none;
    width: 100%;
    margin-bottom: 0;
    height: fit-content;

    &:focus {
      outline: none;
    }
  }

  &__arrow {
    position: absolute;
    right: 17.5px;
    top: 50%;
    margin-top: -10px;
    transform-origin: center;
    transition: 0.3s linear;
  }

  &--active &__arrow {
    transform: rotateZ(-180deg);
  }

  &__value,
  &__placeholder {
    padding: 0;
    outline: none;
    background: transparent;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.5;
    border: none;
    pointer-events: none;
    display: block;
  }

  &__value {
    width: 100%;
    pointer-events: none;
    height: 21px;
  }

  &__placeholder {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  &--active &__placeholder,
  &--valued &__placeholder {
    font-size: 12px;
    color: var(--secondary-darker, #6a6a6a);
    transform: translateY(-110%);
  }

  &--active &__list {
    display: block;
  }
}

.password {
  position: relative;
  padding: 17px 40px 17px 16px;

  &__control {
    position: absolute;
    top: 50%;
    right: 16px;
    width: 20px;
    height: 20px;
    margin-top: -10px;
    cursor: pointer;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__eye-slash {
    display: none;
  }

  &--viewed &__eye-slash {
    display: block;
  }

  &__eye {
    display: block;
  }

  &--viewed &__eye {
    display: none;
  }
}

.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.breadcrumb {
  padding: 4px 8px;
  border-radius: 100px;
  display: inline-flex;
  align-items: center;
  gap: 4px;
  margin-bottom: 0;

  &--danger {
    background: var(--danger-lightest, #fbebeb);
  }

  &--gray {
    background: var(--secondary-lighter, #f5f5f5);
  }

  &--green {
    background: var(--success-lightest, #e8f6ed);
  }

  &--orange {
    background: var(--warning-lightest, #fcf2ea);
  }

  &--blue {
    background: var(--primary-lightest, #ecf6ff);
  }

  span {
    font-size: 12px;
    font-weight: 600;
    line-height: 1.2;
  }

  &--danger span {
    color: var(--danger-darker, #d43232);
  }

  &--gray span {
    color: var(--secondary-darker, #6a6a6a);
  }

  &--green span {
    color: var(--success-darker, #14a34d);
  }

  &--orange span {
    color: var(--warning-darker, #de782e);
  }

  &--blue span {
    color: var(--primary-darker, #1874c1);
  }

  svg {
    width: 12px;
    height: 12px;
    min-width: 12px;
  }

  &--danger svg {
    path {
      fill: var(--danger-darker, #d43232);
    }
  }

  &--gray svg {
    path {
      fill: var(--secondary-darker, #6a6a6a);
    }
  }

  &--green svg {
    path {
      fill: var(--success-darker, #14a34d);
    }
  }

  &--orange svg {
    path {
      fill: var(--warning-darker, #de782e);
    }
  }

  &--blue svg {
    path {
      fill: var(--primary-darker, #1874c1);
    }
  }
}