@use '../abstracts/variables' as var;
@use '../base/mixins' as mixin;

.header {
  &__inner {
    position: relative;
    justify-content: space-between;
  }

  &__btns {
    align-items: center;
  }

  &__btns-line {
    height: 12px;
    width: 1px;
    background: var(--secondary-dark, #ccc);
  }

  &__search-fund {
    padding: 10px 20px;
    background: #000;
    background: var(--primary-lightest, #ecf6ff);
    border-radius: 100px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    color: var(--primary-darker, #1874c1);

    &:hover,
    &:active {
      color: var(--primary-darker, #1874c1);
    }

    @include mixin.small-laptop {
      padding: 8px 14px;
      font-size: 14px;
    }
  }
}

.nav-desktop {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;

  @include mixin.wide-tablet {
    display: none;
  }

  &__list {
    display: flex;
    gap: 32px;
    list-style: none;
    margin: 0;

    @include mixin.small-laptop {
      gap: 18px;
    }
  }

  &__dropdown-item {
    display: block;
  }

  &__item-link-new {
    display: inline-block;
    padding: 3px 6px;
    font-size: 12px;
    font-weight: 600;
    line-height: 1.2;
    color: var(--secondary-lightest, #fff);
    background: var(--danger-darker, #d43232);
    border-radius: 100px;
    margin-left: 6px;
  }

  &__controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;

    @include mixin.small-laptop {
      gap: 14px;
    }

    .header__btns {
      .btn {
        padding: 0;
        font-weight: 600;
        font-size: 16px;
        line-height: 1.2;

        @include mixin.small-laptop {
          font-size: 14px;
        }
      }
    }
  }
}

.nav-mobile {
  display: none;

  @include mixin.wide-tablet {
    display: block;
  }

  .arrow-bottom {
    width: 18px;
    height: 18px;
  }

  .burger-menu__accordion-item:not(.collapsed) {
    svg {
      path {
        fill: var(--primary-darker, #1874C1);
      }
    }
  }
}